import { z } from 'zod'

// CEDEAR form validation schema
export const cedearSchema = z.object({
  symbol: z
    .string()
    .min(1, 'El smbolo es requerido')
    .max(10, 'El smbolo no puede tener ms de 10 caracteres')
    .regex(/^[A-Z0-9.]+$/, 'El smbolo debe contener solo letras maysculas, nmeros y puntos'),
  
  name: z
    .string()
    .min(1, 'El nombre es requerido')
    .max(100, 'El nombre no puede tener ms de 100 caracteres'),
  
  underlyingSymbol: z
    .string()
    .min(1, 'El smbolo subyacente es requerido')
    .max(10, 'El smbolo subyacente no puede tener ms de 10 caracteres')
    .regex(/^[A-Z0-9.]+$/, 'El smbolo subyacente debe contener solo letras maysculas, nmeros y puntos'),
  
  ratio: z
    .number()
    .positive('El ratio debe ser un nmero positivo')
    .min(0.001, 'El ratio mnimo es 0.001')
    .max(1000, 'El ratio mximo es 1000'),
  
  currency: z.enum(['USD', 'ARS'], {
    errorMap: () => ({ message: 'La moneda debe ser USD o ARS' }),
  }),
  
  isESG: z.boolean().default(false),
  
  isVegan: z.boolean().default(false),
  
  sector: z
    .string()
    .min(1, 'El sector es requerido')
    .max(50, 'El sector no puede tener ms de 50 caracteres'),
  
  exchange: z.enum(['BYMA', 'NYSE', 'NASDAQ'], {
    errorMap: () => ({ message: 'El exchange debe ser BYMA, NYSE o NASDAQ' }),
  }),
})

export type CEDEARFormData = z.infer<typeof cedearSchema> & {
  isESG: boolean
  isVegan: boolean
}

// Trade form validation schema
export const tradeSchema = z.object({
  cedearId: z.string().min(1, 'Debe seleccionar un CEDEAR'),
  
  type: z.enum(['BUY', 'SELL'], {
    errorMap: () => ({ message: 'El tipo debe ser Compra o Venta' }),
  }),
  
  quantity: z
    .number()
    .positive('La cantidad debe ser un nmero positivo')
    .min(1, 'La cantidad mnima es 1')
    .max(1000000, 'La cantidad mxima es 1,000,000'),
  
  price: z
    .number()
    .positive('El precio debe ser un nmero positivo')
    .min(0.01, 'El precio mnimo es 0.01')
    .max(100000, 'El precio mximo es 100,000'),
  
  commission: z
    .number()
    .min(0, 'La comisin no puede ser negativa')
    .max(10000, 'La comisin mxima es 10,000')
    .default(0),
  
  date: z
    .date()
    .max(new Date(), 'La fecha no puede ser futura'),
  
  notes: z
    .string()
    .max(500, 'Las notas no pueden tener ms de 500 caracteres')
    .optional(),
})

export type TradeFormData = z.infer<typeof tradeSchema>

// Financial goal form validation schema
export const financialGoalSchema = z.object({
  name: z
    .string()
    .min(1, 'El nombre del objetivo es requerido')
    .max(100, 'El nombre no puede tener ms de 100 caracteres'),
  
  targetAmount: z
    .number()
    .positive('El monto objetivo debe ser positivo')
    .min(1000, 'El monto mnimo es $1,000')
    .max(10000000, 'El monto mximo es $10,000,000'),
  
  currentAmount: z
    .number()
    .min(0, 'El monto actual no puede ser negativo')
    .max(10000000, 'El monto actual mximo es $10,000,000')
    .default(0),
  
  deadline: z
    .date()
    .min(new Date(), 'La fecha lmite debe ser futura'),
  
  category: z.enum(['retirement', 'education', 'house', 'vacation', 'emergency', 'other'], {
    errorMap: () => ({ message: 'Debe seleccionar una categora vlida' }),
  }),
  
  priority: z.enum(['low', 'medium', 'high'], {
    errorMap: () => ({ message: 'Debe seleccionar una prioridad vlida' }),
  }),
})

export type FinancialGoalFormData = z.infer<typeof financialGoalSchema>

// Settings form validation schema
export const settingsSchema = z.object({
  theme: z.enum(['light', 'dark', 'system']),
  
  language: z.enum(['es', 'en']),
  
  currency: z.enum(['ARS', 'USD']),
  
  notifications: z.object({
    priceAlerts: z.boolean(),
    goalProgress: z.boolean(),
    technicalSignals: z.boolean(),
  }),
  
  refreshInterval: z
    .number()
    .min(60, 'El intervalo mnimo es 60 segundos')
    .max(3600, 'El intervalo mximo es 3600 segundos (1 hora)'),
  
  autoAnalysis: z.boolean(),
})

export type SettingsFormData = z.infer<typeof settingsSchema>

// Commission configuration validation schema
export const commissionConfigSchema = z.object({
  name: z
    .string()
    .min(1, 'El nombre de la configuracin es requerido')
    .max(50, 'El nombre no puede tener ms de 50 caracteres'),
  
  type: z.enum(['percentage', 'fixed', 'tiered'], {
    errorMap: () => ({ message: 'El tipo debe ser porcentaje, fijo o por tramos' }),
  }),
  
  value: z
    .number()
    .min(0, 'El valor no puede ser negativo')
    .max(100, 'El valor mximo es 100'),
  
  minAmount: z
    .number()
    .min(0, 'El monto mnimo no puede ser negativo')
    .optional(),
  
  maxAmount: z
    .number()
    .min(0, 'El monto mximo no puede ser negativo')
    .optional(),
})
.refine((data) => {
  if (data.minAmount && data.maxAmount) {
    return data.minAmount <= data.maxAmount
  }
  return true
}, {
  message: 'El monto mnimo debe ser menor o igual al monto mximo',
  path: ['maxAmount'],
})

export type CommissionConfigFormData = z.infer<typeof commissionConfigSchema>

// Search/filter validation schemas
export const searchSchema = z.object({
  query: z.string().max(100, 'La bsqueda no puede tener ms de 100 caracteres'),
  
  filters: z.object({
    isESG: z.boolean().optional(),
    isVegan: z.boolean().optional(),
    sectors: z.array(z.string()).optional(),
    exchanges: z.array(z.string()).optional(),
    currency: z.enum(['USD', 'ARS', 'ALL']).optional(),
  }).optional(),
})

export type SearchFormData = z.infer<typeof searchSchema>